import Vue from 'vue'
import VueSweetalert2 from 'vue-sweetalert2';
import App from './App.vue'
import axios from 'axios'


Vue.config.productionTip = false
Vue.use(VueSweetalert2);

Vue.prototype.$https = axios.create({
  baseURL: 'http://localhost:4000',
  timeout: 5000
});

new Vue({
  render: h => h(App),
}).$mount('#app')
