<template>
  <div>
    <transition
      enter-active-class="animated fadeInUp"
      leave-active-class="animated fadeInDown"
      mode="out-in"
      appear
    >
      <div>
        <h1 id="title" align="center">{{Title}}</h1>
        <hr align="center" :style="{'width':this.lineWidth}" id="underline" />
        <br />
        <div class="vrline">
          <p>{{ logcontents }}</p>
        </div>
        <button v-if="Token !== null" class="swal2-styled" @click.prevent="deleteLog" >Delete</button>
        <button v-if="Token !== null" class="swal2-styled" @click.prevent="Logout" >Logout</button>
      </div>
    </transition>
  </div>
</template>

<script>
import "animate.css";
export default {
  name: "app",
  data() {
    return {
      Title: null,
      logcontents: null,
      Token: null,
      lineWidth: 0
    };
  },
  created() {
    this.Login();
  },
  methods: {
    Login() {
      this.$swal({
      title: 'Multiple inputs',
      html:
        '<input id="swal-input1" class="swal2-input" placeholder="Username">' +
        '<input id="swal-input2" class="swal2-input" placeholder="Password">',
      focusConfirm: false,
      allowOutsideClick: false,
      onClose: () => {
        var loginDetails = {
        username: document.getElementById('swal-input1').value,
        password: document.getElementById('swal-input2').value
      }
      this.$https
        .post("/users/authenticate", loginDetails)
        .then(res => {
          this.Token = res.data.token;
          this.getContents();
        })
        .catch(err => {
          setTimeout(() => {
            this.Title = "Username/Password Incorrect!";
          }, 2);
          this.Login();
        });
    }
      
      });
    },
    getContents() {
      this.$https
        .post("/", null, {
          headers: {
            Authorization: "Bearer " + this.Token
          }
        }).then(res => {
          this.Title = "Contents";
          this.logcontents = res.data.message;
        })
        .catch(err => {
          this.Title = "Something went wrong :(";
          this.Logout();
        });
    },
    deleteLog() {
      this.$https
        .post("/delete", { password: this.password }, {
          headers: {
            Authorization: "Bearer " + this.Token
          }
        }).then(res => {
          this.Title = "Contents";
          this.logcontents = res.data.message;
        })
        .catch(err => {
          this.$swal("Operation Failed! " + err.message, '', 'warning');
        });
    },
    Logout() {
      this.Token = null;
      this.Title = null;
      this.logcontents = null;
      this.Login();
    }
  },
  watch: {
    Title() {
      this.lineWidth = this.Title.length + 4 + "rem";
    }
  }
};
</script>

<style>
html body {
  background-color: black;
  color: aliceblue;
  font-family: google;
}
@font-face {
  font-family: "google";
  src: url("../google.ttf");
}
#underline {
  background: #dddd00;
  border-color: #000000;
  height: 2px;
  padding: 0;
}
.vrline {
  border-left: 2px #ec3135 solid;
  padding: 0px 7px 0px 4px;
  white-space: pre;
}
</style>

